@import url(https://fonts.googleapis.com/css2?family=Ranchers&display=swap);
body{background:#373B44;background:linear-gradient(to right, #4286f4, #373B44);font-family:'Ranchers', cursive}#GameBoard{position:relative;margin:auto;background-color:aliceblue;border-radius:20px;border:1px solid #E922EC;box-shadow:inset 0 0 1rem #E922EC}#GameBoard .Block{position:absolute}#GameBoard #GameOver{text-align:center;height:100%;-webkit-flex-direction:column;flex-direction:column;display:-webkit-flex;display:flex;-webkit-justify-content:space-around;justify-content:space-around}#GameBoard #Score{position:relative;top:105%;text-align:center;font-weight:bold}#GameBoard #GameOverText{color:#ca0000;font-weight:bold;-webkit-text-decoration-line:underline;text-decoration-line:underline}#GameBoard #PressSpaceText{color:#2F0C05;font-weight:bold}

